import React from "react";
import { css } from "@emotion/react";
import { Link, graphql } from "gatsby";
import { rhythm } from "../utils/typography";
import Layout from "../components/layout";
//import "highlight.js/styles/atom-one-dark.css";
//import "highlight.js/styles/atom-one-light.css";

const LectureIndex = ({ data }) => {
  const { edges: lectures } = data.allMdx;
  const [lightMode, setLightMode] = React.useState(
    typeof window != "undefined" && sessionStorage.getItem("lightMode") != null
  );

  function changeTheme() {
    if (sessionStorage.getItem("lightMode")) {
      sessionStorage.removeItem("lightMode");
      setLightMode(false);
    } else {
      sessionStorage.setItem("lightMode", "true");
      setLightMode(true);
    }
  }

  return (
    <Layout>
      <div>
        <h1
          css={css`
            display: inline-block;
            border-bottom: 1px solid;
          `}
        >
          {data.site.siteMetadata.title}
        </h1>
        <p
          css={css`
            font-size: 1.3em;
            margin-top: -0.5em;
          `}
        >
          {data.site.siteMetadata.authors.map((author, index) => {
            return (
              <>
                {index > 0 && " & "}
                <Link to={author.link}>{author.name}</Link>
              </>
            );
          })}
        </p>
        <input
          type="checkbox"
          checked={lightMode}
          onChange={changeTheme}
          id="lightModeCheck"
          css={css`
            margin-right: ${rhythm(1 / 4)};
          `}
        />
        <label htmlFor="lightModeCheck">Lightmode</label>
        {lectures.map(({ node: lecture }) => (
          <div key={lecture.id}>
            <Link
              to={lecture.fields.slug + "0"}
              css={css`
                text-decoration: none;
                color: inherit;
              `}
            >
              <h3
                css={css`
                  margin-bottom: ${rhythm(1 / 4)};
                `}
              >
                {lecture.frontmatter.title}{" "}
                <span
                  css={css`
                    color: #555;
                  `}
                >
                </span>
              </h3>
            </Link>
          </div>
        ))}
      </div>
    </Layout>
  );
};
/* Übersicht mit Datum:
<h3
  css={css`
    margin-bottom: ${rhythm(1 / 4)};
  `}
>
  {lecture.frontmatter.title}{" "}
  <span
    css={css`
      color: #555;
    `}
  >
    — {lecture.frontmatter.date}
  </span>
</h3>
*/

//<h4>{data.allMdx.totalCount} Stunden</h4>
//<p>{lecture.excerpt}</p>

export const pageQuery = graphql`
  query lectureIndex {
    allMdx(sort: { fields: [frontmatter___date], order: ASC }) {
      totalCount
      edges {
        node {
          id
          excerpt
          frontmatter {
            title
            date(formatString: "DD. MMMM YYYY", locale: "de")
          }
          fields {
            slug
          }
        }
      }
    }
    site {
      siteMetadata {
        title
        authors {
          name
          link
        }
      }
    }
  }
`;

export default LectureIndex;
